<template>
  <div class="footer">
    <input type="text" placeholder="Enter Name..." v-model="name" />
    <input type="text" placeholder="Enter Phonenumber..." v-model="number" />

    <i class="fas fa-user-plus" @click="createPerson"></i>
  </div>
</template>

<script>

export default {
  name: "Footer",
  data() {
    return {
      name: "",
      number: "",
    };
  },
  methods: {
    createPerson() {
      if(this.name == '' || this.number == ''){
        return alert("Enter valid name and number")
      }
      this.$emit("item-add", this.name, this.number);

      this.name = "";
      this.number = "";
    },
  },
};
</script>

<style scoped>
.footer {
  background: rgba(255, 255, 255, 0.3);
  min-height: 10vh;
  width: 350px;
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.footer input {
  background: rgba(255, 255, 255, 0.3);
  height: 50px;
  border-radius: 0px 16px 0px 16px;
  transition: 0.4s;
  font-size: 21px;
  margin: 10px auto;
  appearance: none;
  outline: none;
  border: none;
  padding: 10px;
}
.footer input:focus {
  background: rgba(255, 255, 255, 0.6);
  border-radius: 16px 0px 16px 0px;
}
.footer input:hover{
    background: rgba(255, 255, 255, 0.6);
}
.footer i {
  color: white;
  font-size: 35px;
  margin: 10px auto;
  background: rebeccapurple;
  width: 300px;
  padding: 10px;
  border-radius: 5px;
  transition: 0.4s;
}
.footer i:hover {
  color: rebeccapurple;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 16px 0px 16px 0px;
  transform: scale(1.05);
  cursor: pointer;
}
</style>
