class Person{
    constructor(name,number){
        this.id = Math.random().toString(36).substring(7);
        this.name = name;
        this.number = number;
    }
    static fromJSON(json){
        let person = new Person();
        person.id = json.id;
        person.name = json.name;
        person.number = json.number;

        return person;
    }
}

export default Person;