<template>
  <div id="app">
    <Upperbody :cb="contacts"/>
    <Footer @item-add="contactAdd"/>
  </div>
</template>

<script>
import Upperbody from './components/Upperbody.vue'
import Footer from './components/Footer.vue'
import Person from '../classes/Person'
import Contacts from '../classes/Contacts'

export default {
  name: 'App',
  components: {
    Upperbody,
    Footer
  },
  data() {
    return{
      contacts : new Contacts()
    }
  },
  methods: {
    contactAdd(name,number){
      let contact = new Person(name,number);
      this.contacts.add(contact)
    }
  },
}
</script>

