<template>
      <div class="person" >
        <i class="fas fa-phone-square"><a :href="`tel:${contact.number}`"></a></i>
        <div class="name">
          {{contact.name}}
        </div>
        <div class="number">
          {{contact.number}}
        </div>
        <i class="fas fa-user-minus" @click.stop="deleteItem"></i>
      </div>
</template>

<script>
export default {
    name: 'Contact',
    props: ["contact"],
    methods:{
      deleteItem(){
        this.$emit('delete-item')
      }
    }
}
</script>

<style scoped>
.person {
  display: flex;
  margin: 10px;
  border-bottom: 1px solid black;
}
.person i {
  margin: 0px 10px;
  color: green;
  cursor: pointer;
  transition: 0.5s;
}
.person i:hover {
  transform: scale(1.1);
  color: rgb(53, 53, 3);
}
.person .name {
  margin: 0 auto;
}
.person .number {
  margin: 0 auto;
}
.fa-user-minus {
  color: red;
}
</style>